___CSS_LOADER_EXPORT___.push([module.id, "\n._2IGAZexUUINQcfr0NvuvlI {\n\tpadding-left: 15px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/GatewaySettings.vue"],"names":[],"mappings":";AAgIA;CACA,kBAAA;AACA","sourcesContent":["<template>\n\t<div>\n\t\t<div v-if=\"!isAvailable\">\n\t\t\t<L10n text=\"The Email two-factor authentication method is not available. Do you have an email address set?\" />\n\t\t</div>\n\t\t<div v-else-if=\"loading\">\n\t\t\t<span class=\"icon-loading-small\" />\n\t\t</div>\n\t\t<div v-else>\n\t\t\t<p v-if=\"state === states.DISABLED\">\n\t\t\t\t<slot name=\"instructions\" />\n\t\t\t\t<L10n text=\"You are not using Email as a two-factor authentication method at the moment.\" />\n\t\t\t\t<button @click=\"enable\">\n\t\t\t\t\t<L10n text=\"Enable\" />\n\t\t\t\t</button>\n\t\t\t</p>\n\t\t\t<p v-if=\"state === states.CREATED\">\n\t\t\t\t<strong v-if=\"verificationError === true\">\n\t\t\t\t\t<L10n text=\"Could not verify your code. Please try again.\" />\n\t\t\t\t</strong>\n\t\t\t\t<L10n text=\"A confirmation code has been sent to {emailAddress}. Please insert the code here:\"\n\t\t\t\t\t:options=\"{emailAddress: emailAddress}\" />\n\t\t\t\t<input v-model=\"confirmationCode\">\n\t\t\t\t<button @click=\"confirm\">\n\t\t\t\t\t<L10n text=\"Verify\" />\n\t\t\t\t</button>\n\t\t\t\t<button @click=\"disable\">\n\t\t\t\t\t<L10n text=\"Cancel\" />\n\t\t\t\t</button>\n\t\t\t</p>\n\t\t\t<p v-if=\"state === states.ENABLED\">\n\t\t\t\t<L10n text=\"Your account was successfully configured to receive authentication codes via Email to {emailAddress}.\"\n\t\t\t\t\t:options=\"{emailAddress: emailAddress}\" />\n\t\t\t\t<button v-if=\"!isEnforced\"  @click=\"disable\">\n\t\t\t\t\t<L10n text=\"Disable\" />\n\t\t\t\t</button>\n\t\t\t</p>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport L10n from './L10n.vue'\nimport {\n\tstartVerification,\n\ttryVerification,\n\tdisable,\n} from '../service/registration'\nimport { loadState } from 'nextcloud-initial-state'\n\nexport default {\n\tname: 'GatewaySettings',\n\tcomponents: {\n\t\tL10n,\n\t},\n\tdata() {\n\t\tconst STATE = {\n\t\t\tDISABLED: 0,\n\t\t\tCREATED: 1,\n\t\t\tENABLED: 2,\n\t\t}\n\n\t\treturn {\n\t\t\tloading: false,\n\t\t\tstates: STATE,\n\t\t\tstate: STATE.DISABLED,\n\t\t\temailAddress: '',\n\t\t\tisAvailable: true,\n\t\t\tconfirmationCode: '',\n\t\t\tverificationError: false,\n\t\t\tisEnforced:false,\n\t\t}\n\t},\n\tmounted: function() {\n\t\tthis.isAvailable = loadState('twofactor_email', 'available')\n\t\tconst state = loadState('twofactor_email', 'state')\n\t\tthis.state = state.state\n\t\tthis.emailAddress = state.emailAddress\n\t\tthis.isEnforced = state.isEnforced\n\t},\n\tmethods: {\n\t\tenable: function() {\n\t\t\tthis.loading = true\n\t\t\tthis.verificationError = false\n\t\t\tstartVerification()\n\t\t\t\t.then(res => {\n\t\t\t\t\tthis.state = this.states.CREATED\n\t\t\t\t\tthis.emailAddress = res.emailAddress\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t\t\t.catch(e => {\n\t\t\t\t\tconsole.error(e)\n\t\t\t\t\tthis.state = this.states.DISABLED\n\t\t\t\t\tthis.verificationError = true\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t},\n\t\tconfirm: function() {\n\t\t\tthis.loading = true\n\n\t\t\ttryVerification(this.confirmationCode)\n\t\t\t\t.then(res => {\n\t\t\t\t\tthis.state = this.states.ENABLED\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t\t\t.catch(res => {\n\t\t\t\t\tthis.state = this.states.CREATED\n\t\t\t\t\tthis.verificationError = true\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t},\n\n\t\tdisable: function() {\n\t\t\tthis.loading = true\n\n\t\t\tdisable()\n\t\t\t\t.then(res => {\n\t\t\t\t\tthis.state = res.state\n\t\t\t\t\tthis.emailAddress = res.emailAddress\n\t\t\t\t\tthis.loading = false\n\t\t\t\t})\n\t\t\t\t.catch(console.error.bind(this))\n\t\t},\n\t},\n}\n</script>\n\n<style>\n\t.icon-loading-small {\n\t\tpadding-left: 15px;\n\t}\n</style>\n"],"sourceRoot":""}]);
